.box {
    background-color: #ffffff;
    border: none;
    border-radius: 10%;
    box-shadow: 0px 0px 8px #888888;
    width: 5rem;
    height: 5rem;
    text-align: center;
    font-size: 5em;
    font-family: "Fredoka", sans-serif;
    font-weight: bold;
    line-height: 5rem;
    margin: 0.5rem;
}

.x {
    color: rgb(255, 70, 37);
}

.o {
    color: rgb(44, 135, 255);
}

.box:hover {
    box-shadow: 0px 0px 15px #888888;
}

.board {
    display: grid;
    grid-template-columns: repeat(3, 6rem);
    place-items: center;
    justify-content: center;
}

.scoreboard {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
    width: 20rem;
    font-size: 1.5rem;
    background-color: white;
    margin: 3rem auto;
    box-shadow: 0px 0px 8px #888888;
    border-radius: 0.5rem;
    font-weight: bold;
}

.score {
    width: 100%;
    text-align: center;
    padding: 1rem 0rem;
}

.x-score {
    color: rgb(255, 70, 37);
    border-bottom: 5px solid rgb(255, 70, 37);
    border-radius: 0.5rem 0rem 0rem 0.5rem;
}

.o-score {
    color: rgb(44, 135, 255);
    border-bottom: 5px solid rgb(44, 135, 255);
    border-radius: 0rem 0.5rem 0.5rem 0rem;
}

.inactive {
    border-bottom: 5px solid transparent;
}

.reset{
    margin-top: 30px;
    background-color: rgb(122, 163, 207);
    width: 170px;
    font-size: 50px;
    font-style: italic;
    border-radius: 8px;
    height: 80px;
    text-align: center;
    place-items: center;
    justify-content: center;
}

.game{
    margin-top: 400px;
    justify-content: center;
    text-align: center;
    place-items: center;
 
}

body{
